import React, { useEffect, useState } from 'react'
import styled from 'styled-components';
import { Link, useParams } from 'react-router-dom';
import axios from 'axios';

export default function Todolist() {
  const [todos, setTodos] = useState(null);

  const fetchTodos = async () => {
    const { data } = await axios.get("http://localhost:5000/posts");
    setTodos(data); //  fetching  useState state set .
  };

  //    mount     useEffect .
  useEffect(() => {
    // effect     .
    fetchTodos();
  }, []);

  console.log(todos)

  const deleteBtn= (e) => {
    axios.delete(`http://localhost:5000/posts/${e.target.id}`);
  };

  return (
    <div>
      <h1 style={{marginLeft: '50px'}}> </h1>
        {todos && todos.map((item, index) => (
              <TodoBox key={index.id}>
            <div className='TodoList'>
              <h2>: {item.title}</h2>
              <p>:{item.maker}</p>
              <p>:{item.content}</p>
              
            </div>
            <div className='myBtn'>
              <button
              type='button'
              id={item.id}
              onClick={deleteBtn}></button>
            </div>
         </TodoBox>
        ))}
    </div>
  )
}

const TodoBox = styled.div`
  border: 1px solid black;
  border-radius: 15px;
  width: 90vw;
  height: 25vh;
  margin-left: 50px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;


  .TodoList > h2 {
    margin-left: 10px;
  }

  .TodoList > p {
    margin-left: 10px;
  }

  .myBtn {
    margin-top: 50px;
    margin-right: 10px;
  }
  .myBtn > button {
    background-color: yellowgreen;
    padding: 10px;
    border: none;
    border-radius: 15px;
  }
  `;